import axios from 'axios';


// Function to get CSRF token from cookies
const getCSRFToken = () => {
    const name = 'csrftoken';
    const value = document.cookie.match('(^|;)\\s*' + name + '=([^;]*)');
    return value ? value.pop() : '';
  };
  
// Function to send OTP via Twilio WhatsApp API
// Function to send OTP
export const sendOTP = async (phoneNumber) => {
    try {
      const csrfToken = getCSRFToken();  // Retrieve CSRF token from cookies
  
      const response = await axios.post('/api/auth/send_otp/', 
        { phone_number: phoneNumber },
        {
          headers: {
            'Content-Type': 'application/json',
            'X-CSRFToken': csrfToken  // Include CSRF token in request headers
          },
          withCredentials: true  // Ensure cookies are sent with the request
        }
      );
  
      return response.data;
    } catch (error) {
      console.error('Error sending OTP:', error.response ? error.response.data : error.message);
      throw error;
    }
  };

// Function to verify OTP
export const verifyOTP = async (phoneNumber, otp) => {
  try {
    const response = await axios.post('/api/auth/verify_otp/', { phone_number: phoneNumber, otp });
    return response.data;
  } catch (error) {
    console.error('Error verifying OTP:', error);
    throw error;
  }
};
